import React, { useState } from 'react';

import Animation from '../../../hoc/Animation/Animation';

import classes from './Catalog.module.css';

const Catalog = ({ data, maxVisible, expandText, collapseText }) => {
    const [expand, setExpand] = useState(false);

    const timeConverter = (time) => {
        let result = '';
        
        if(Math.trunc(time / 60) > 0) 
            result += Math.floor(time / 60) + ' hour';

        if(Math.trunc(time / 60) > 1)
            result += 's';

        if(result !== '')
            result += ' ';

        if(time % 60 !== 0)
            result += time % 60 + ' min';

        return result;
    };

    const itemsList = () => {
        let list = data.map(item =>
            <li className = { classes.Item } key = { item.name }>
                <Animation>
                    <div className = { classes.Line }>
                        <p className = { classes.Name }>{ item.name }</p>
                        <p className = { classes.Price }>£{ item.price }</p>
                    </div>
        
                    <p className = { classes.Time }>{ timeConverter(item.time) }</p>
                </Animation>
            </li>)
    
        if(expand) list.length = data.length;
        else list.length = maxVisible;

        return list;
    };
 
    return (
        <div className = { classes.Catalog }>
            <ul className = { classes.List }>
                { itemsList() }
            </ul>

            { maxVisible < data.length && 
                <Animation>
                    <button
                        className = { classes.Button }
                        onClick = { () => setExpand(!expand) }>
                        { expand ? collapseText : expandText }
                    </button>
                </Animation> }
        </div>
    )
};

export default Catalog;
